<template>
	<div>
		<div class="tab">
			<li :class="[topIndex==0?'active':'']" @click="change(0)">全部</li>
			<li :class="[topIndex==1?'active':'']" @click="change(1)">我的</li>
			<div class="topLine" :style="line"></div>
		</div>
		<div v-if="topIndex==0">
			<div class="item" v-for="item in list" @click="go(item.id)">
				<div class="top">
					<div class="avatar">
						<img :src="item.image"  />
					</div>
					<div class="right">
						<p style="font-size: 0.3rem;">{{item.name}}</p>
						<p style="color: #999999;font-size: 0.24rem;">
							<span>{{item.time}}</span>
							<span style="float: right;">回复({{item.num}})</span>
						</p>
					</div>
				</div>
				<div class="bottom">
					{{item.content}}

				</div>
			</div>
			
		

		</div>
		<div v-if="topIndex==1">
            <div class="item" v-for="item in list" @click="go(item.id)">
				<div class="top">
					<div class="avatar">
						<img :src="item.image"  />
					</div>
					<div class="right">
						<p style="font-size: 0.3rem;">{{item.name}}</p>
						<p style="color: #999999;font-size: 0.24rem;">
							<span>{{item.time}}</span>
							<span style="float: right;">回复({{item.num}})</span>
						</p>
					</div>
				</div>
				<div class="bottom">
					{{item.content}}

				</div>
			</div>
			<div v-if="list.length>1" style="padding: 0.3rem;">
				
				<div class="noDataBtn" @click="release">发布留言</div>
			</div>
			
			<div class="noData" v-if="list.length<1">
				<div class="noDataBox">
					<div class="icon">

					</div>
					<p>您还没有发布留言</p>
					<div class="noDataBtn" @click="release">发布留言</div>
				</div>

			</div>

		</div>

		<Bottom></Bottom>

	</div>
</template>

<script>
	import Bottom from '../../components/Bottom/Bottom'
	export default {
		data() {
			return {
				topIndex: 0,
				  id:this.$route.params.ConsoledateId,
				  list:[]
			}
		},
		components: {
			Bottom
		},
		mounted(){
			this.getData(1)
			
		},
		computed: {
			line() {
				if(this.topIndex == 0) {
					return {
						"left": "20%"
					}
				} else {
					return {
						"left": "70%"
					}
				}

			}
		},
		methods: {
			
			go(id){
				
				this.$router.push({name: 'CommunicateItem',params:{CommunicateId:id}})
			},
			
			change(num) {
                this.$loading('正在提交评分');
                var that=this
               
			
                setTimeout(function(){
                	
                	
              	 that.$loading('正在提交评分','close');
                	 that.topIndex = num
                     that.getData(num+1)
                },800)
				
				
				
				
			},
			release(){
				
				this.$router.push({name: 'Release',params:{ConsoledateId:this.id}})
			},
			getData(num){
				
				var obj={
					"s":this.$store.state.sessionId,
					"id":this.id,
					"a":num
				}
			   this.$Api.LeaveWordList(obj).then((res)=>{
			   	
			   	
			   	  	 this.list=res.q.leaveword
			   	  for(var i=0;i< this.list.length;i++){
			   	  	this.list[i].image="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1522300298011&di=e4b387e3c04d878c58d6cb54385a73a6&imgtype=0&src=http%3A%2F%2Fwww.qqzhi.com%2Fuploadpic%2F2014-09-23%2F000247589.jpg"
			   	  	
			   	  }
			   	  
			   	 
			   	 
			   	 
			   	  
			   })
				
				
			}
			
			
			
		}
	}
</script>

<style lang="scss" scoped="scoped">
	@import "../../sass/baseSass.scss";
	.tab {
		width: 100%;
		height: 0.9rem;
		border-bottom: 0.02rem solid #CCCCCC;
		position: relative;
		li {
			width: 50%;
			height: 100%;
			float: left;
			list-style: none;
			text-align: center;
			line-height: 0.9rem;
			font-size: 0.3rem;
		}
		.active {
			color: #FA8A12;
		}
		.topLine {
			width: 0.8rem;
			height: 0.04rem;
			background: #FA8A12;
			position: absolute;
			bottom: 0.01rem;
			left: 20%;
			transition: 0.5s;
		}
	}
	
	.item {
		border-bottom: 0.02rem solid #CCCCCC;
		.top {
			height: 1.4rem;
			padding: 0.3rem 0.3rem 0.2rem 0.3rem;
			box-sizing: border-box;
			.avatar {
				width: 0.9rem;
				height: 0.9rem;
				background: red;
				border-radius: 50%;
				float: left;
				img{
					width: 100%;
					height: 100%;
					float: left;
					border-radius:50% ;
				}
			}
			.right {
				width: 82%;
				float: left;
				font-size: 0.1;
				margin-left: 0.2rem;
				margin-top: 0.05rem;
			}
		}
		.bottom {
			font-size: 0.3rem;
			padding: 0 0.3rem 0.2rem 0.3rem;
		}
	}
	
	.noDataBox {
		width: 2.4rem;
		height: 4rem;
		margin: 1rem auto;
		font-size: 0.28rem;
		color: #666666;
		.icon {
			width: 1.24rem;
			height: 1.11rem;
			background: url(../../../static/message.png) no-repeat;
			background-size: 100% 100%;
			margin: auto;
		}
		p {
			margin-top: 0.4rem;
			text-align: center;
		}
	}
	.noDataBtn{
		width: 2rem;
		height: 0.7rem;
		background: #fa8a12;
		color: #FFFFFF;
		text-align: center;
		line-height: 0.7rem;
		font-size: 0.3rem;
		margin:1.35rem auto;
		
		border-radius:0.7rem ;
	}
</style>


